import React, { useContext } from "react";
import { StoreContext } from "../../../store/StoreProvider";
import { itemsPerPage } from "../Shop";

const Item = ({
  category,
  activePage,
  isItemInCart,
  setIsItemInCart,
  itemAddedToCart,
  setItemAddedToCart,
}) => {
  const { items, cartStatus, setCartStatus } = useContext(StoreContext);

  const alreadyInCartAlert = (
    <div class="alert alert-danger my-2" role="alert">
      Produkt znajduje się już w koszyku
    </div>
  );

  const addToCartAlert = (
    <div class="alert alert-success" role="alert">
      Dodano produkt do koszyka
    </div>
  );

  const itemToShow = (title, description, type, price, id, kind) =>
    items ? (
      <div className="col-md-4">
        <div className="card mb-4">
          <img
            src={`/images/${kind}.jpg`}
            alt={title}
            className="card-img-top"
          />
          <div className="card-body">
            <h3 className="card-title mb-0">
              {title}
              {type === "new" && <span className="badge-success">New!</span>}
            </h3>
            <p className="">
              {description.substring(0, description.indexOf(".") + 1)}
            </p>
          </div>
          <div className="card-footer text-center">
            {price} $ id {id}
            <div>
              <button>dodaj do ulubionych</button>
            </div>
            <div>
              <button
                data-value={id}
                onClick={(e) => {
                  const itemId = e.target.getAttribute("data-value");
                  const selectedItem = items.filter((i) =>
                    itemId == i.id ? i : null
                  );

                  const isAlreadyInCart = cartStatus.filter((i) => {
                    if (i.id == id) return i;
                  });

                  if (isAlreadyInCart.length > 0) {
                    setIsItemInCart(id);
                    // setTimeout(() => {
                    //   setIsItemInCart(0);
                    // }, 3000);
                    return;
                  } else {
                    setItemAddedToCart(id);
                    setCartStatus(cartStatus.concat(selectedItem));
                  }
                }}
              >
                dodaj do koszyka
              </button>
              {isItemInCart === id && alreadyInCartAlert}
              {itemAddedToCart === id && addToCartAlert}
            </div>
          </div>
        </div>
      </div>
    ) : (
      <p>
        Błąd podczas pobierania przedmiotów. Prosze odświeżyć stronę bądź
        spróbować później{" "}
      </p>
    );

  const errorMsg = (
    <p>
      Błąd podczas pobierania przedmiotów. Prosze odświeżyć stronę bądź
      spróbować później
    </p>
  );

  const fn = () => {
    if (items) {
      const itemsToShow = items
        .filter((i) => {
          if (category === "all" || i.category === category) {
            return i;
          }
        })
        .map((item) => {
          let kind = item.category;
          const { title, description, type, price, id } = item;
          return itemToShow(title, description, type, price, id, kind);
        });
      if (itemsToShow.length < itemsPerPage) {
        return itemsToShow;
      } else if (itemsToShow.length >= itemsPerPage) {
        const smallArray = itemsToShow.filter((i, index) => {
          if (
            index >= itemsPerPage * (activePage - 1) &&
            index < itemsPerPage * activePage
          ) {
            return i;
          }
        });
        return smallArray;
      } else {
        return errorMsg;
      }
    } else {
      return errorMsg;
    }
  };

  //   useEffect(() => {
  //     const urlParams = new URLSearchParams(window.location.search);
  //     urlParams.set("page", "1");
  //     history.push(window.location.pathname + "?" + urlParams.toString());
  //   }, []);

  return <>{fn()}</>;
};

export default Item;
